body {
  margin: 0;
  padding: 0;
  width: 100%;
}

.nav {
  background-color: rgba(255, 255, 255, 0.119);
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  padding: 10px 0;
}
@media screen and (min-width: 1200px) {
  .nav {
    justify-content: end;
    padding: 0;
  }
}
.nav__toggle {
  position: absolute;
  cursor: pointer;
  right: 0;
}
.nav .fa-xmark {
  display: none;
  color: white;
  font-size: 34px;
}
@media screen and (min-width: 1200px) {
  .nav .fa-xmark {
    display: none;
  }
}
.nav .fa-bars {
  color: white;
  font-size: 34px;
  margin: 0 10px;
}
@media screen and (min-width: 1200px) {
  .nav .fa-bars {
    display: none;
  }
}
.nav__checkbox {
  display: none;
}
.nav__menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media screen and (min-width: 1200px) {
  .nav__menu {
    flex-direction: row;
  }
}
.nav__menu li {
  list-style-type: none;
  display: none;
  color: white;
  text-align: center;
  font-size: 20px;
}
@media screen and (min-width: 1200px) {
  .nav__menu li {
    display: block;
    margin: 0 20px;
  }
}
.nav__menu a {
  text-decoration: none;
  color: white;
  font-size: 18px;
}
.nav__menu a:hover {
  text-decoration: underline;
}

#nav__checkbox:checked ~ ul.nav__menu li {
  display: block;
}

#nav__checkbox:checked ~ label.nav__toggle .close {
  display: block;
}

#nav__checkbox:checked ~ label.nav__toggle .menu {
  display: none;
}

.hero {
  height: 100vh;
  background-image: url(./../images/negative-space-black-forest-dawn.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
}
.hero__container {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
}
.hero__title {
  color: white;
  text-align: center;
}
.hero__subtitle {
  color: white;
  font-size: 24px;
  font-weight: lighter;
  text-align: center;
  padding: 0 100px;
}

.arrow-first {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.arrow-first .fa-sharp {
  color: white;
  font-size: 24px;
}

.team {
  width: 100%;
}
.team__title {
  text-align: center;
}

.cards {
  width: 100%;
}

.card {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media screen and (min-width: 1200px) {
  .card {
    width: 50%;
    height: 700px;
    float: left;
    flex-wrap: nowrap;
  }
}
.card__title {
  display: flex;
  justify-content: center;
  text-align: center;
}
.card__imagecontainer {
  width: 100%;
  display: flex;
  justify-content: center;
}
.card__imagecontainer__image {
  width: 50%;
  border-radius: 50%;
  box-shadow: 5px 5px 30px 5px gray;
}

.box {
  background-color: lightgray;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 70%;
  height: 15vh;
  border-radius: 2%;
  margin: 20px 0;
}
@media screen and (min-width: 1200px) {
  .box {
    width: 50%;
    padding: 0 50px;
    float: left;
    flex-wrap: nowrap;
  }
}
.box__quote {
  padding: 40px 10px;
  margin: 20px;
}
.box__quote .fa-sharp {
  font-size: 36px;
}

.arrow-second {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
@media screen and (min-width: 1200px) {
  .arrow-second {
    margin: 20px 0;
  }
}
.arrow-second .fa-sharp {
  color: black;
  font-size: 24px;
}

footer {
  height: 60vh;
  background-color: black;
  display: flex;
  flex-direction: column;
  padding: 40px;
}
@media screen and (min-width: 1200px) {
  footer {
    flex-direction: row;
    height: 30vh;
    padding: 0;
  }
}

.container {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 1200px) {
  .container {
    display: block;
    margin: 0 auto;
    padding: 40px 0;
  }
}
.container__contact {
  color: white;
  list-style-type: none;
  font-size: 20px;
  padding: 10px 0;
}

.fa-brands {
  padding: 0 10px;
}